body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-3xl: 22px;
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --font-size-sm: 14px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-gray: #888;
  --color-white: #fff;
  --color-gainsboro-100: #e5e3da;
  --color-gainsboro-200: #e0e0e0;
  --color-tomato: #ed553b;
  --color-darkslateblue: #393280;
  --color-whitesmoke: #eaeaea;
  --color-linen: #eae8df;
}
